import { ChangeEvent, DragEvent } from "react";

import { Margins } from "../styleguide";

export type FileUploaderWrapperProps = {
	showValidationError?: boolean;
	dragActive: boolean;
	isDisabled?: boolean;
	uploadButtonIsFocused: boolean;
};

export type FileUploaderProps = Pick<FileUploaderWrapperProps, "isDisabled"> & Margins & {
	dataQA: string;
	onDrop: (event: DragEvent<HTMLDivElement> | null) => void;
	onChange: (event: ChangeEvent<HTMLInputElement> | null) => void;
	subtitle?: string;
	className?: string;
	/**
	 * @see https://developer.mozilla.org/en-US/docs/Web/HTML/Attributes/accept
	 */
	accept?: string;
	errorMessage?: string;
};
